.admin-vehicle,
.admin-main {
  &__details {
    &-title {}
    &-subtitle {
      padding: 0;
      margin: 10px 0; } }


  &__customer-details {
    &-info {
      padding: 0 5px; } }


  &__main {
    width: 100%; }

  & .details-info-table {
    margin: 0 0 20px;
    padding: 0 15px 0;
    &__row {
      height: auto; } }


  &__add {
    padding: 0 15px;
    &-offer {
      padding: 0 10px 0;
      margin-bottom: 30px;
      &-descr {
        display: inline-block;
        font-size: 14px;
        padding: 0 5px; }

      &-offer,
      &-buyer {
        display: block;
        width: 100%;
        height: 32px; }

      &-submit {
        height: 32px;
        margin-top: 21px; } } }



  &__update {
    &-time {
      display: block;
      width: 100%;
      height: 32px; } } }


